@media only screen and (min-width: 1032px) {
  .slider__img{
    padding: .5rem 2em;
    display: grid;
    grid-template-columns: auto auto auto auto;
  }

}
.small-nav{
  padding: 1rem 2rem 0 2rem;
  display: none;
}
@media only screen and (min-width: 532px) {
  .slider-content{
    background-color: rgb(252, 252, 252);
    max-height: 100%;
    width: 305px;
    position: relative;
  }
  .nav-content { 
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgb(231, 231, 231);
    padding: 0 2rem;
  }
  .nav-icon{
    min-width: 100px;
    display: grid;
    align-items: center;
    opacity: .6;
    transition: .2s all ease;
  }
  .nav-icon a{
    display: grid;
    text-align: center;
    place-items: center;
    font-size: .8rem;
    line-height: 2rem;
  }
}
@media only screen and (max-width: 1332px) {
  .slider__img{
    padding: .5rem 0;
    display: grid;
    grid-template-columns: auto auto auto auto;
  }
}
@media only screen and (max-width: 1172px) {
  .slider__img{
    padding: .5rem 5rem;
    display: grid;
    grid-template-columns: auto auto auto;
  }
}
@media only screen and (max-width: 1120px) {
  .slider__img{
    padding: .5rem 1rem;
    display: grid;
    grid-template-columns: auto auto auto;
  }
}
@media only screen and (max-width: 1020px) {
  .slider__img{
    padding: .5rem 5rem;
    display: grid;
    grid-template-columns: auto auto;
  }
  .nav-content .navigation {
    margin: 0;
  }
}
@media only screen and (max-width: 800px) {
  .slider__img{
    padding: .5rem 2rem;
    display: grid;
    grid-template-columns: auto auto;
  }
  .nav-content { 
    display: none;
  }
  .small-nav{
    padding: 1rem 2rem 0 2rem;
    display: block;
  }
}
@media only screen and (max-width: 700px) {
  .slider__img{
    padding: .5rem .5rem;
    display: grid;
    grid-template-columns: auto auto;
  }
}
@media only screen and (max-width: 680px) {
  .slider__img{
    padding: .5rem .5rem;
    display: grid;
    grid-template-columns: auto;
    justify-content: center;
  }
  .slider-content{
    background-color: rgb(252, 252, 252);
    max-height: 100%;
    min-width: 405px;
    position: relative;
  }
  .nav-icon{
    min-width: 70px;
    display: grid;
    align-items: center;
    opacity: .6;
    transition: .2s all ease;
  }
  .nav-icon a{
    display: grid;
    text-align: center;
    place-items: center;
    font-size: .7rem;
    line-height: 2rem;
  }
}


.slider__img{
  gap: 1rem;
}

.slider__img{
  justify-content: center;
}





*{
  padding: 0;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

body{
  height: 100vh;
  width: 100%;
}

nav{
  z-index: 2;
  min-width: 100%;
  position: relative;
  background: #fff;
}

.main-content{

}

.nav-content img{
  width: 90px;
  object-fit: cover;
}

.nav-content, .nav-button-content{
  background: #fff;
}

.pf{
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
}

.navigation{
  border-radius: 8rem;
  padding: 0 1rem;
  padding-right: .5rem;
  margin-left: 8rem;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 1.2px 8px;
}

.navigation input{
  font-size: 1rem;
  padding: .7rem;
  background: none;
  border: none;
  font-weight: 500;
  cursor: pointer;
  border-right: 1px solid rgb(206, 206, 206);
  border-radius: 55px 0;
  padding-right: 1.5rem;
}

.navigation i {
  color: #fff;
  background-color: #ff385c;
  padding: .5rem;
  border-radius: 8rem;
}

.pf label{
  font-weight: 500;
  color: #ff385c;;
}

.pf{
  align-items: center;
}




















.nav-button-content{
  max-width: 100%;
  display: flex;
  gap: .5rem;
  overflow: hidden;
  justify-content: center;
  padding: 1.7rem 0;
}

.nav-icon img{
  width: 26px;
}

.nav-icon:hover{
  opacity: 1;
}
.nav-icon label{
  cursor: pointer;
}



.border-bottom{
  background: rgb(133, 133, 133);
  width: 100px;
  height: 1px;
  border-radius: 8px;
  opacity: 0;
}

.nav-icon a:hover ~ .border-bottom {
  opacity: 1;
}



.nav-btn {
  border: 1px solid rgb(226, 226, 226);
  margin: 1rem 0;
  border-radius: 8px;
  padding: .7rem;
  align-items: center;
  gap: .5rem;
  position: absolute;
  right: 15px;
  top: 5px;
}

.nav-btn-cover{
  min-height: 100%;
  width: 120px;
  position: absolute;
  right: 0;
  z-index: 1;
  background: white;
}









/* Slider*/






img{
  max-width: 100%;
}

.slider-img img{
  height: 300px;
  min-width: 160px;
  object-fit: cover;
  border-radius: .5rem;
}

.lguFiu button{
  position: absolute;
  z-index: 1;
}

.rec-arrow-right{
  position: absolute;
  right: 15px;
}
.rec-arrow-left{
  position: absolute;
  left: 15px;
}

.slider-text-bottom{
  padding: .5rem 1rem;
  display: grid;
}

.text-bottom-header{
  display: flex;
  justify-content: space-between;
}

.slider-text-bottom span{
  color: rgb(138, 138, 138);
  font-weight: lighter;
}

.fa-heart{
  position: absolute;
  right: 65px;
  z-index: 2;
  top: 20px;
  color: white;
  opacity: .9;
  font-size: 1.2rem;
}

.pf-btn{
  border-radius: 8rem;
  padding: 0 1rem;
  padding-right: .5rem;
  cursor: pointer;
  border: 1px solid rgb(226, 226, 226);
  display: flex;
  align-items: center;
  gap: .5rem;
  justify-content: center;
  min-width: 70px;
  padding: .6rem;
}

.fa-bars{
  padding-left: .3rem;
}

.pf-btn:hover{
  box-shadow: rgba(0, 0, 0, 0.24) 0px 1.2px 8px;
}

.pf-btn .fa-user{
  background-color: grey;
  color: #fff;
  width: 25px;
  height: 25px;
  border-radius: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
















.small-nav-content{
  display: flex;
  border-radius: 8rem;
  font-size: .9em;
  align-items: center;
  gap: 1em;
  position: relative;
  padding: .7em .9em;
  box-shadow: rgba(50, 50, 93, 0.123) 0px 6px 12px -2px, rgba(0, 0, 0, 0.096) 0px 3px 7px -3px;
  cursor: pointer;
  z-index: 2;
}

.fil{
  position: absolute;
  right: 10px;
  border: 1px solid rgb(216, 216, 216);
  height: 31px;
  width: 32px;
  border-radius: 8em;
  display: flex;
  align-items: center;
  justify-content: center;
}




.fa-heart{
  z-index: 1;
}















.popup{
  position: relative;
  opacity: 0;
}

.toggler{
  display: block;
}

.popup-content{
  position: absolute;
  z-index: 6;
  background: #f7f7f7;
  max-width: 100%;
  height: 650px;
}

.where-content{
  box-shadow: rgba(50, 50, 93, 0.123) 0px 6px 12px -2px, rgba(0, 0, 0, 0.096) 0px 3px 7px -3px;
  background: #fff;
  margin: 0 1em;
  border-radius: 2em;
  display: grid;
  overflow: hidden;
  padding: 0 1em;
}

.text_input{
  display: grid;
}

.where-content img{
  width: 119px;
  border-radius: 8px;
  border: 1px solid rgb(219, 219, 219);
}
.country{
  margin: 1.2em 0 1.2em 1.2em;
}

.popup-header{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 1.4em 0;
}

.close-btn {
  position: absolute;
  left: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 31px;
  border-radius: 8rem;
}

.close-btn i{
  font-size: 1em;
  cursor: pointer;
}

.head-text{
  display: flex;
  gap: 1rem;
  font-weight: 600;
}

.text_input{
  margin: 1.2em 0;
  margin-bottom: 0;
  position: relative;
}

.text_input input{
  padding: .7em;
  border-radius: 8px;
  padding-left: 2.6em;
  border: 1px solid rgb(177, 177, 177);
}

.text_input i{
  position: absolute;
  top: 57px;
  left: 15px;
  cursor: pointer;
}

.popup-bottom{
  margin: 0 1.2em;
  padding: .7em 0;
}

.a{
  background: #fff;
  margin: 1em 0;
  padding: 1em;
  border-radius: .7em;
  display: flex;
  justify-content: space-between;
}

.clear-search{
  background: rgb(255, 255, 255);
  padding: 1em;
  position: absolute;
  bottom: 0;
  min-width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.clear-search button{
  background-color: #e51e53;
  color: #fff;
  padding: .7em 1.3em;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: .4em;
  font-weight: 600;
}